.App {
  background-color: #ffe7e7;
  text-align: center;
}

.SearchField {
  /* text box to search */
  padding-top: 20px;
  /* background: radial-gradient(circle, black, white); */
  padding-bottom: 20px;
}

.App-header {
  /* Header where it says search GIPHY */
  background: url(https://media4.giphy.com/headers/juanbilly/awbw8EaeaHtc.gif); 
  /* make into random gif? */
  height: 180px;
  background-size: 700px;
  background-repeat: no-repeat;
  background-attachment:scroll;
  background-position: center;
  font-family: Impact;


  /* background-color: #5c4aff; */
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  color: rgb(253, 1, 253);
}

.gif{
  height: 20vh;
}

.nav-bar {
  display: flex;
  justify-content: center;
  padding: 10px;
  margin-bottom: 20px;
}

.nav-button {
  margin: 5px 5px 5px 5px;
  padding: 10px 30px;
  background-color: rgb(253, 1, 253) ;
  border: 0;
  color: white;
  font-size: 18px;
}

/* SEARCH */

#search-field{
  background-color: black;
  margin: auto;
  padding: 20px;
  width: 50%;
  border-radius: 50px;
}

#search-text{
  font-size: 20px;
  margin-right: 10px;
  color:white;
}

#search-input{
  padding: 5px;
  margin-right: 10px;
  font-size: 16px;
}

#search-btn{
  padding: 5px 10px;
}

/* FILTER */

#filter-field{
  background-color: #ffe7e7;
  margin: auto;
  padding: 20px;
}

#filter-text{
  font-size: 20px;
  margin-right: 10px;
}

#dropdown{
  padding: 5px;
  margin-right: 10px;
  font-size: 16px;
}

#filter-btn{
  padding: 5px 10px;
}